const validate = values => {
    const errors = {};
    const { info, cmt } = values;
    if (!info) {
        errors.info = 'Vui lng nhp tn';
    } else if (info.trim() && info.length < 1) {
        errors.info = 'Tn phi t 8 k t tr ln';
    }

    if (!cmt) {
        errors.cmt = 'Vui lng nhp bnh lun';
    } else if (cmt.trim() && cmt.length < 3) {
        errors.cmt = 'Bnh lun phi t 16 k t tr ln';
    }
    return errors;
};

export default validate;