import WheelComponent from "react-wheel-of-prizes";
import "react-wheel-of-prizes/dist/index.css";

export default function LuckWheel() {
  const segments = [
    "Coco Chanel",
    "Son Dior",
    "500.000 VN",
    "IPHONE 13",
    "Macbook Pro",
    "IPAD PRO",
    "Apple watch",
    "5.000.000 VN",
    "Nhn LOTR",
  ];
  const segColors = [
    "orange",
    "rgb(0, 190, 190)",
    "#815CD1",
    "violet",
    "#017fff",
    "orange",
    "#815CD1",
    "violet",
    "#017fff",
  ];
  const onFinished = (winner) => {
    if (winner === "Macbook Pro") {
      alert(
        "Chc mng bn  nhn c con: MacBook Pro 2021 xn s gi 32.490.000 VN !"
      );
    } else if (winner === "IPAD PRO") {
      alert("Chc mng bn  nhn c: IPAD PRO gi 8.999.000 VN ! ");
    } else if (winner === "Coco Chanel") {
      alert(
        "Chc mng bn  nhn c: 1 l nc hoa Chanel gi 4.950.000 VN "
      );
    } else if (winner === "Son Dior") {
      alert(
        "Chc mng bn  nhn c 1 thi Son Dior Rouge gi 1.500.000 VN "
      );
    } else if (winner === "500.000 VN") {
      alert("Chc mng bn  nhn c: 500.000 VN ");
    } else if (winner === "IPHONE 13") {
      alert(
        "Chc mng bn  nhn c con: Iphone 13 Pro Max v.256GB xn s gi 49.999.000 VN !"
      );
    } else if (winner === "Apple watch") {
      alert("Chc mng bn  nhn c: APPLE WATCH gi 17.590.000 VN ");
    } else if (winner === "5.000.000 VN") {
      alert("Chc mng bn  nhn c: 5.000.000 VN ");
    } else if (winner === "Nhn LOTR") {
      alert(
        "Chc mng bn  nhn c: 1 chic nhn cha c sc mnh thao tng tt c vn vt "
      );
    }
  };
  return (
    <>
      <WheelComponent
        segments={segments}
        segColors={segColors}
        onFinished={(winner) => onFinished(winner)}
        primaryColor="rgb(240, 73, 12)"
        contrastColor="white"
        buttonText="QUAY"
        isOnlyOnce={false}
        size={183}
        upDuration={100}
        downDuration={200}
      />
    </>
  );
}
