import { numberFormat, totalMoney } from 'contants/NumberFormat';
import React from 'react';
import { useSelector } from 'react-redux';
import { Link } from "react-router-dom";
import { productsCartMenuState$ } from 'redux/selectors';

function ContactDeliver() {
    const sale = 10000
    const ship = 30000
    const productsCart = useSelector(productsCartMenuState$)

    return (
        <div className="col-md-3 pr-0">
            <div className="bg-white p-3">
                <div className="row justify-content-around">
                    <h6>a ch giao hng</h6>
                    <button type="button" className="btn btn-success btn-sm" style={{ fontSize: '12px' }}>Thay i</button>
                </div>
                <hr />
                <div>
                    <h6>L Xun Hong</h6>
                    <p className="mb-0">a ch: H ng- H Ni</p>
                    <p className="mb-0">in thoi: 0986380270</p>
                </div>
            </div>
            <div className="bg-white mt-3 p-2">
                <div className="row  pl-3 pr-3 p-2 justify-content-between">
                    <div>
                        <h6>n hng</h6>
                        <small>1 sn phm</small>
                    </div>
                    <button type="button" className="btn btn-success btn-sm" style={{ fontSize: '12px', height: '28px' }}>Thay i</button>
                </div>

                <div className="row  pl-3 pr-3 p-2 justify-content-between">
                    <div>
                        <span>Tm tnh</span>
                    </div>
                    <h6>{numberFormat(totalMoney(productsCart))}</h6>
                </div>

                <div className="row  pl-3 pr-3 p-2 justify-content-between">
                    <div>
                        <span>Gim gi</span>
                    </div>
                    <h6>-{numberFormat(sale)}</h6>
                </div>

                <div className="row  pl-3 pr-3 p-2 justify-content-between">
                    <div>
                        <span>Ph vn chuyn</span>
                    </div>
                    <h6>{numberFormat(ship)}</h6>
                </div>
                <hr />

                <div className="row  pl-3 pr-3 p-2 justify-content-between">
                    <div>
                        <span>Thnh tin</span>
                    </div>
                    <h5 className="text-danger">{numberFormat(totalMoney(productsCart) - sale + ship)}</h5>
                    <div className="w-100 text-right">
                        <small className="text-secondary">( bao gm VAT nu c)</small>
                    </div>
                </div>
            </div>
            <Link to="/dat-hang-thanh-cong">
                <button type="button" className="btn btn-danger btn-md btn-block mt-3 mb-1">Tin hnh thanh ton</button>
            </Link>
            <div className="text-right">
                <small className="text-secondary">( Vui lng kim tra n hng trc khi thanh ton )</small>
            </div>
        </div>
    );
}

export default ContactDeliver;